import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { AngularFireAuth } from 'angularfire2/auth';
import { LoginPage } from '../login/login';
import { UserProfilePage } from '../user-profile/user-profile';

import { FirebaseListObservable, AngularFireDatabase } from 'angularfire2/database-deprecated';
import { Person } from '../../model/person';
import { person } from '../person/person';
import { TostServiceProvider } from '../../providers/tost-service/tost-service';
import { PersonProvider } from '../../providers/person/person';
import { Http } from '@angular/http';
import { user } from '../user/user';
import { Observable } from '../../../node_modules/rxjs/Observable';


@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
    

    Persons : FirebaseListObservable<any>;
   
constructor(public fire:AngularFireAuth,
            public navCtrl: NavController,
            public db:AngularFireDatabase,
            private personProvider: PersonProvider,
            public http: Http) {
                this.Persons = db.list('/people')

    fire.auth.onAuthStateChanged(function(user){
if(!user){
    navCtrl.setRoot(LoginPage)
}
        });
  
  }

  public findAllPersons(): Observable<user[]> {
    return this.db.object('people').map(user.fromJsonListUser);
  }

  deletePerson(id) {
    this.Persons.remove(id);
  }

/*editPerson(id,username, name, lname) {
   // console.log(  id, name,  lname, dept);
    this.navCtrl.push(EditPersonPage,{
    key : id,
    username : username,
    name : name,
    lname : lname
   
        });

}*/

}




