import { ReportProvider } from '../../providers/report/report';
import { Report,image  } from '../../model/report';
import { TostServiceProvider } from '../../providers/tost-service/tost-service';
import { TabsPage } from '../tabs/tabs';
import { File } from '@ionic-native/file';
import { HomeshowPage} from '../homeshow/homeshow';
import * as firebase from 'firebase/app';

declare var google: any;
declare var window: any;


@Component({
  selector: 'page-report',
  templateUrl: 'report.html'
})

export class ReportPage {
  
  map: any;
  infoWindow : any;

  private userAuth: any;
  private pictures: any[];
  
 
  reports: Report[];
  report: Report = { category: "", topics: "", comment: "",  lat: "",  lng: "", 
  MyData: "" ,email: "",status :"" , email_unfix :"", email_fixing : "",  
  email_fixed : "", image: "" };
 

  public photos = [];
  public base64Image: any;

  private coords: any;
  private lat: any;
  private lng: any;
  private accuracy: any;
  private error: any;
  public marker: any;
  public MyDate : any;
  private opts: string;
  private addH: string;
  private addCom: string;
  private latlng: string;
  private resText: string;
  private image : any;
  public URL : any;
  public uploadSnapshot  : any;
  
  
  private basePath: string = '/report';
  objectToSave: Array<any> = new Array;

  private takePictureOptions: CameraOptions = {
    allowEdit: false,
    saveToPhotoAlbum: true,
    targetWidth: 720,
    targetHeight: 720,
    cameraDirection: this.camera.Direction.BACK,
    sourceType: this.camera.PictureSourceType.CAMERA,
    destinationType: this.camera.DestinationType.FILE_URI,
  }

  private galleryOptions: CameraOptions = {
    allowEdit: true,
    destinationType: this.camera.DestinationType.FILE_URI,
    sourceType: this.camera.PictureSourceType.PHOTOLIBRARY,
    targetWidth: 720,
    targetHeight: 720,
    correctOrientation: true
  }
  
 
  @ViewChild('map') mapElement: ElementRef;
 
  
Date;
MyData;
  constructor(
    public navCtrl: NavController,
    private platform: Platform, 
    private geolocation: Geolocation,
    private camera: Camera,
    public alertCtrl: AlertController,
    public app: App,
    public loadingCtrl: LoadingController,
    public Tost: TostServiceProvider,
    private reportProvider: ReportProvider,
    private actionSheetCtrl: ActionSheetController,
    private file: File
    
    

  ) {

    this.Date = new Date();
    this.getMyData();
    this.image = '';

  }


  ionViewDidLoad() {
  
    this.loadMap();
    this.validate();

  }

  saveDetails(category, topics, comment, lat, lng, status ) {


    let loader = this.loadingCtrl.create({
      content: "รอสักครู่ ...",
      duration: 1000
    });
    loader.present();
    this.report = {
      category: category,
      topics: topics,
      comment: comment,
      lat: lat,
      lng: lng,
      MyData : this.MyData,
      email:sessionStorage.getItem("email"),
      status : status,
      email_unfix :  sessionStorage.getItem("email"), 
      email_fixing : sessionStorage.getItem("email"), 
      email_fixed :  sessionStorage.getItem("email"),
      image: this.image
    };
    try {
      
      this.reportProvider.pushObject(this.report).then(res=>{
        this.Tost.showToastWithCloseButton("บันทึกข้อมูลสำเร็จ");
        this.navCtrl.push(ShowrePage);
      });

    } catch (error) {
      this.Tost.showToastWithCloseButton(error);
    }


  }


  logout() {
    // Remove API token
    sessionStorage.removeItem("email");
    this.navCtrl.setRoot(HomeshowPage);
    const root = this.app.getRootNav();
    root.popToRoot();
    let alert = this.alertCtrl.create({
      title: 'ออกจากระบบ',
      message: 'ต้องการออกจากระบบ?',
      buttons: [
        {
          text: 'ยกเลิก',
          role: 'ยกเลิก',
          handler: () => {
            console.log('Cancel clicked');
          }
        },
        {
          text: 'ใช่',
          handler: () => {
            console.log('Buy clicked');
          }
        }
      ]
    });
    alert.present();
  
  }

  onChange(SelectedValue) {
    console.log("Selected type", SelectedValue);
    this.validate();
  }


  cencel() {
    this.navCtrl.push(ReportPage);
  }

  deletePhoto(index) {

    let confirm = this.alertCtrl.create({
      title: 'ต้องการลบรูปภาพนี้',
      message: '',
      buttons: [
        {
          text: 'ยกเลิก',
          handler: () => {
            console.log('Disagree clicked');
          }
        }, {
          text: 'ตกลง',
          handler: () => {
            console.log('Agree clicked');
            this.photos.splice(index, 1);
            //return true;
          }
        }
      ]
    });
    confirm.present();
  }

//ถ่ายรูปภาพ
  takePhoto(): void {
    let actionSheet = this.actionSheetCtrl.create({
      enableBackdropDismiss: true,
      buttons: [
        {
          text: 'ถ่ายรูปภาพ',
          icon: 'camera',
          handler: () => {
            this.uploadFromCamera();
          }
        }/*, {
          text: 'เพิ่มรูปภาพ',
          icon: 'images',
          handler: () => {
            this.uploadFromGallery();
          }
        }*/
      ]
    });
    actionSheet.present();
  }

  refreshPicture(): void {
    var result = [];
    // load data from firebase...
    firebase.database().ref('report').orderByKey().once('value', (snapshot: any) => {
      snapshot.forEach((childSnapshot) => {
        // get the key/id and the data for display
        var element = childSnapshot.val();
        element.id = childSnapshot.key;
        result.push(element);
      });
      this.pictures = result;
      
    });
  }

  isReadonly() {
    return this.isReadonly;   //return true/false 
  }

 uploadFromCamera() {
  let loader = this.loadingCtrl.create({
    content: "รอสักครู่ ...",
    duration: 10000
  });
  loader.present();
    this.camera.getPicture(this.takePictureOptions).then((imagePath) => {
      return this.makeFileIntoBlob(imagePath);
    }).then((imageBlob) => {
      return this.uploadToFirebase(imageBlob);
    }).then((uploadSnapshot: any) => {
      loader.dismiss();
      this.image = uploadSnapshot.downloadURL;
      return uploadSnapshot; 
    }).then((_uploadSnapshot: any) => {
      alert('บันทึกรูปภาพเรียบร้อยแล้ว');
    }, (_error) => {
      alert('Error ' + (_error.message || _error));
    });
  }

  makeFileIntoBlob(_imagePath) {
    return new Promise((resolve, reject) => {
      window.resolveLocalFileSystemURL(_imagePath, (fileEntry) => {

        fileEntry.file((resFile) => {

          var reader = new FileReader();
          reader.onloadend = (evt: any) => {
            var imgBlob: any = new Blob([evt.target.result], { type: 'image/jpeg' });
            imgBlob.name = 'sample.jpg';
            resolve(imgBlob);
          };

          reader.onerror = (e) => {
            console.log('Failed file read: ' + e.toString());
            reject(e);
          };

          reader.readAsArrayBuffer(resFile);
        });
      });
    });
  }

  
  uploadToFirebase(imgBlob: any) {
    var randomNumber = Math.floor(Math.random() * 256);
    console.log('Random number : ' + randomNumber);
    return new Promise((resolve, reject) => {
      let storageRef = firebase.storage().ref(this.basePath + randomNumber + '.jpg');

      let metadata: firebase.storage.UploadMetadata = {
        contentType: 'image/jpeg',
      };

      let uploadTask = storageRef.put(imgBlob, metadata);
      uploadTask.on(firebase.storage.TaskEvent.STATE_CHANGED,
        (snapshot) => {
          
           let progress = (uploadTask.snapshot.bytesTransferred / uploadTask.snapshot.totalBytes) * 100;
          console.log(progress);
        },
        (error) => {
         
          console.log(error);
          reject(error);
        },
        () => {
         
          let url = uploadTask.snapshot.downloadURL
          console.log('Saved picture url', url);
          resolve(uploadTask.snapshot);
        });
    });
  }



  // ========= ฟังก์ชัน validate ข้อมูล =========
  async validate() {
    // =========ร้องขอข้อมูล Report จาก reportProvider แล้วเก็บไว้ใน this.reports =========
    await this.reportProvider.getList().subscribe(list => {
      // console.log("list", list);
      this.reports = list;
    });
    // console.log("this.reports", this.reports);
    // คีย์ที่ต้องการใช้ในการค้นหา
    let filters = {
      
      lat: [this.lat],
      lng: [this.lng],
      opts: [this.opts]

    };
    // ฟังก์ชันค้นหาข้อมูลใน Array this.multiFilter(อาเรย์, คีย์เวิร์ด)
    let result = this.multiFilter(this.reports, filters);
    // console.log("result",result);
    if (result.length > 0) {
      this.lat = "";
      this.lng = "";
      this.opts = "";

      alert('ตำแหน่งนี้มีผู้รายงานแล้วค่ะ');
   //   this.Tost.showToastWithCloseButton("ตำแหน่งนี้มีผู้รายงานแล้วค่ะ");


    }

  }

  // ========= ฟังก์ชันค้นหาข้อมูใน Array โดยใส่ Array และ Key =========
  multiFilter(array, filters) {
    const filterKeys = Object.keys(filters);
    // filters all elements passing the criteria
    return array.filter((item) => {
      // dynamically validate all filter criteria
      return filterKeys.every(key => !!~filters[key].indexOf(item[key]));
    });
  }

  
  loadMap() {
    this.platform.ready().then(()=>{

      this.geolocation.getCurrentPosition().then((position) => {
     //  console.log('lat '+position.coords.latitude);  
        let latLng = new google.maps.LatLng(position.coords.latitude, position.coords.longitude);
        let mapOptions = {
          center: latLng,
          zoom: 15,
          mapTypeId: google.maps.MapTypeId.ROADMAP
        }
        this.lat=position.coords.latitude;
        this.lng=position.coords.longitude;
        this.map = new google.maps.Map(this.mapElement.nativeElement, mapOptions);
        let marker = new google.maps.Marker({
          map: this.map,
          animation: google.maps.Animation.DROP,
          position: this.map.getCenter()
        });
      }).catch((error) => {
        this.error = 'Error getting location: ' + JSON.stringify(error);  
      
      
      });

    });

  }


  getMyData(){
    var dateObj = new Date()
    var date = dateObj.getDate().toString()
    var month = dateObj.getMonth().toString()
    var year = dateObj.getFullYear().toString()

    this.MyData = date +'/' + month +'/' + year ;
  }

}
 


