import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { GarbagePage} from '../garbage/garbage';
import { LightPage} from '../light/light';
import { RoadPage} from '../road/road';
import { WaterPage} from '../water/water';
import { TreePage} from '../tree/tree';
import { OtherPage} from '../other/other';

@Component({ template: `
<ion-list>
  <button ion-item (click)="garbage()">ขยะ</button>
  <button ion-item (click)="light()">ไฟฟ้าและแสงสว่างภายนอกอาคาร</button>
  <button ion-item (click)="road()">ถนน</button>
  <button ion-item (click)="water()">ระบายน้ำและปะปา</button>
  <button ion-item (click)="tree()">สวน</button>
  <button ion-item (click)="other()">อื่นๆ</button>
</ion-list>
`
})
export class PopoverTypePage {

  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  garbage(){
    this.navCtrl.push(GarbagePage);
  }

  light(){
    this.navCtrl.push(GarbagePage); 
  }
  road(){
    this.navCtrl.push(GarbagePage);
  }
  water(){
    this.navCtrl.push(GarbagePage);
  }
  tree(){
    this.navCtrl.push(GarbagePage);
  }
  other(){
    this.navCtrl.push(GarbagePage);
  }

}



