import { Component } from '@angular/core';
import { IonicPage, NavController } from 'ionic-angular';
import { AngularFireAuth } from 'angularfire2/auth';
import "rxjs/add/operator/map";
import { FirebaseListObservable, AngularFireDatabase } from 'angularfire2/database-deprecated';
import { LoadingController } from 'ionic-angular';
import { TostServiceProvider } from '../../providers/tost-service/tost-service';

import { PopoverController } from 'ionic-angular';
 


@Component({
  selector: 'page-fixing',
  templateUrl: 'fixing.html',
})
export class FixingPage {
  public myUnFixList: Array<{category: string, comment:string, lat: number, lng: number, 
    status: string, topics: string, email_fixing : string , myDate?: string , image? : string }>;

    MyStatus : string;


  persons : FirebaseListObservable<any>;

  constructor(public fire:AngularFireAuth,
    public navCtrl: NavController,
    public db:AngularFireDatabase,
    public loadingCtrl: LoadingController,
    public Tost: TostServiceProvider,
    public popoverCtrl:PopoverController) {

    this.persons = db.list('/report');
    this.persons.subscribe(obj => {
      for(let i = 0; i < obj.length; i++){
        
        if (i === 0 || typeof this.myUnFixList === "undefined"){
          if (obj[i].status === "กำลังแก้ไข"){
            this.myUnFixList = [{
              category: obj[i].category,
              comment: obj[i].comment,
              lat: obj[i].lat,
              lng: obj[i].lng,
              status: obj[i].status,
              topics: obj[i].topics,
              email_fixing : obj[i].email_fixing,
              image:obj[i].image,
              myDate: (obj[i].MyData===''? '':obj[i].MyData)
            }];
          }
        }else{
          if (obj[i].status === "กำลังแก้ไข"){
            this.myUnFixList.push({
              category: obj[i].category,
              comment: obj[i].comment,
              lat: obj[i].lat,
              lng: obj[i].lng,
              status: obj[i].status,
              topics: obj[i].topics,
              email_fixing : obj[i].email_fixing,
              image:obj[i].image,
              myDate: (obj[i].MyData===''? '':obj[i].MyData)
            });
          }
        }
      }
    });

    /*"ยังไม่ได้แก้ไข" "กำลังแก้ไข" "แก้ไขเรียบร้อยแล้ว"*/
}

}



