export class reported {
    constructor(
      public $Key      :   String,
      public email     :   String,
      public category  :   String,
      public topics    :   String,
      public comment   :   String, 
      public MyData    :   String,
      public status    :   String
      ) {
  }
  
  static fromJsonListstatus(array):reported[]{
     return array.map(reported.fromJsonStatus);
  }
  static fromJsonStatus({$key,email,category,topics,comment,MyData,status}):reported {
    return new reported(
          $key,
          email,
          category,
          topics,
          comment,
          MyData,
          status
        
          );
   }
  }


