import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { FirebaseListObservable, AngularFireDatabase } from 'angularfire2/database-deprecated';
import { Observable } from '../../../node_modules/rxjs/Observable';
import { person } from '../person/person';
import { EditdatashowPage } from '../editdatashow/editdatashow';

import { AlertController } from 'ionic-angular';
@Component({
  selector: 'page-editshowre',
  templateUrl: 'editshowre.html',
})
export class EditshowrePage {
  persons : FirebaseListObservable<any>;
  public login = {email:sessionStorage.getItem("email")};
  constructor(public navCtrl: NavController, 
              public navParams: NavParams,
              public db:AngularFireDatabase,
              public alertCtrl: AlertController            
        ) {
          
          this.persons = db.list('/report', {
            query: {
              orderByChild: 'email',
              equalTo: this.login.email
            }
          })
  }

  public findAllPersons(): Observable<person[]> {
    return this.db.list('report')
    .map(person.fromJsonList);
  
  } 
  deletePerson(id){

    const confirm = this.alertCtrl.create({
      message: 'ต้องการลบหรือไม่?',
      buttons: [
        {
          text: 'ใช่',
          handler: () => {
            this.persons.remove(id);
            console.log('clicked');
          }
        },
        {
          text: 'ไม่',
          handler: () => {
            this.navCtrl.push(EditshowrePage);
            console.log('No clicked');
          }
        }
      ]
    });
    confirm.present();
  }
    
  editPerson(id,category,topics, comment,email,MyData, image) {
   {
      this.navCtrl.push(EditdatashowPage,{
        key      : id,
        category : category,
        topics    : topics,
        comment : comment,
        email : email,
        MyData : MyData,
        image     : image

        
            });

    }

    
      }
    
      
}



