import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { AngularFireAuth } from 'angularfire2/auth';
import { AboutPage} from '../about/about';
import { TostServiceProvider } from '../../providers/tost-service/tost-service';
import { LoadingController } from 'ionic-angular';



@Component({
  selector: 'page-register',
  templateUrl: 'register.html',
})
export class RegisterPage {
 

email:string = '';
password : string = '';
 myError='';

constructor(public fire:AngularFireAuth ,public navCtrl: NavController,
   public navParams: NavParams ,  public Tost: TostServiceProvider,public loadingCtrl: LoadingController
   ) {
      
  }


myRegister(){


    this.fire.auth.createUserWithEmailAndPassword(this.email, this.password).then(user=>{
      let loader = this.loadingCtrl.create({
        content: "รอสักครู่ ...",
        duration: 1000
      });
      loader.present();

   //   this.Tost.showToastWithCloseButton("บันทึกข้อมูลสำเร็จ");
        this.navCtrl.push(AboutPage);

      console.log(this.email +"  "+this.password)
      this.myError = this.email +"  "+this.password;
      this.navCtrl.push(AboutPage);

}).catch(function(error) {
  // Handle Errors here.
   console.log(error);
// ...
});



}


















}



