import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Observable } from '../../../node_modules/rxjs/Observable';
import { FirebaseListObservable } from 'angularfire2/database-deprecated';
import { AngularFireDatabase } from 'angularfire2/database-deprecated';
import { reported } from '../reported/reported';


@Component({
  selector: 'page-showstatus',
  templateUrl: 'showstatus.html',
})
export class ShowstatusPage {

  statuss : FirebaseListObservable<any>;

  constructor(public navCtrl: NavController, 
              public navParams: NavParams,
              public db:AngularFireDatabase) {
                this.statuss = db.list('/reported')
  }
  public findAllPersons(): Observable<reported[]> {
    return this.db.list('reported')
  //   this.persons = db.list('report') 
  //  .do(console.log)
    .map(reported.fromJsonListstatus);
   // console.log(this.findAllPersons.toString);
  } 

}



