import { Component, ViewChild, ElementRef } from '@angular/core';
import { IonicPage, NavController, NavParams, App, AlertController, PopoverController, Select} from 'ionic-angular';
import { Http } from '@angular/http';
import { AngularFireAuth } from 'angularfire2/auth';
import { LoginPage } from '../login/login';
import { ShowrePage } from '../showre/showre';

import { ReportProvider } from '../../providers/report/report';
import { Report } from '../../model/report';
import { AngularFireDatabase } from 'angularfire2/database-deprecated';
import { ReportPage} from '../report/report';
import { LoadingController } from 'ionic-angular';
import { PopovereditPage } from '../popoveredit/popoveredit';
import { PopoverTypeePage } from '../popover-typee/popover-typee';
declare var google;

@Component({
  selector: 'page-homeshow',
  templateUrl: 'homeshow.html',
})
export class HomeshowPage {
  category: string;

  @ViewChild('map') mapElement: ElementRef; 
  @ViewChild('loadtype') loadtype: Select;
  
  map: any;
  reports: Report[];
  public login = {user:sessionStorage.getItem("email")};
  constructor(
    public fire: AngularFireAuth,
    public navCtrl: NavController,
    public db: AngularFireDatabase,
    public http: Http,
    public app : App,
    private reportProvider: ReportProvider,
    public alertCtrl : AlertController,
    public loadingCtrl: LoadingController,
    public popoverCtrl:PopoverController
  )
  
   {
    
   
  }
  

  ionViewDidLoad() {
    // =========ฟังก์ชันร้องขอข้อมูล Report จาก Provider=========
    //this.getMarkers();
    // =========ฟังก์ชันแสดง GoogleMap =========
    this.displayGoogleMap();
  }

  openLogin() {
    this.navCtrl.push(LoginPage);
    
  }

  openReport(){
    this.loadingCtrl.create({
      content: "รอสักครู่ ...",
      duration: 10000
    });
    this.navCtrl.push(ReportPage);
    

  }

  signout() {
    // Remove API token
    sessionStorage.removeItem("email");
    this.navCtrl.setRoot(HomeshowPage);
    const root = this.app.getRootNav();
    root.popToRoot();
    this.loadingCtrl.create({
      content: "รอสักครู่ ...",
      duration: 10000
    });
  
  }

  show() {
    this.navCtrl.push(ShowrePage);
  }
  
  displayGoogleMap() {
    let latLng = new google.maps.LatLng(19.0265673, 99.895268);
    console.log(latLng);
    let mapOptions = {
      center: latLng,
      zoom: 14,
      mapTypeId: google.maps.MapTypeId.ROADMAP
    }
    this.mapElement.nativeElement.innerHTML = '';
    this.map = new google.maps.Map(this.mapElement.nativeElement, mapOptions);
    this.reportProvider.getList({
      query:{
        orderByChild: 'category',
        equalTo: this.category
      }
    }).subscribe(list => {
      // console.log("list", list);
      this.reports = list;
      console.log("this.reports", this.reports);

     
      this.addMarkersMap(this.reports);
    });
  }

 // getMarkers() {
    // =========ร้องขอข้อมูล Report จาก reportProvider แล้วเก็บไว้ใน this.reports =========
   
 // }
  groupBy(array:Report[],lat,lng){
    let groub = [];
    for (let item of array) {
        if(item.lat==lat&&item.lng==lng){
          let conten = {
            $Key  :item.$Key,
            email :item.email,
            comment:item.comment,
            topics:item.topics,
            image : item.image,
            MyData:item.MyData,
            category:item.category,
            status:item.status
          }
          groub.push(conten);
        }
    }
    return groub;
  }
  
  addMarkersMap(reports: Report[]) {
    let infowindow = new google.maps.InfoWindow();
    let marker;

    for (let report of reports) {
  
      // =========เพิ่ม Marker ลง Google Map =========
      var loc = { lat: report.lat, lng: report.lng };
      var icon = this.pinIcon(report.category);
      // console.log(loc);
      marker = new google.maps.Marker({
        position: loc,
        map: this.map,
        icon:icon 
      });
      let customContent = '<div class="wd_info-contact">'+
                            '<h3> '+report.topics+' </h3>'+
                              '<img src='+report.image+ ' style="width: 200px; height: 150px;"/> ' +
                              '<span style="color: #222;">'+
                              '<br>ประเภทของรายงานปัญหา :'+report.category+
                              '<br>รายละเอียดปัญหา :'+report.comment+
                              '<br>วันที่รายงานปัญหา :'+report.MyData+
                              '<br>ผู้รายงานปัญหา :'+report.email+
                              '<br>สถานะ :'+report.status+
                              '</span>'+
                           
                          '</div>';
      google.maps.event.addListener(marker, 'click', (function (marker, content, infowindow) {
        return function () {
          infowindow.setContent(content);
          infowindow.open(this.map, marker);
        };
      })(marker, customContent, infowindow));
    }
  

  }

  pinIcon(category){
    switch(category){
     case "ขยะ":{
      return 'http://maps.google.com/mapfiles/ms/icons/red-dot.png';
     }
     case "ไฟฟ้าและแสงสว่างภายนอกอาคาร":{
      return 'http://maps.google.com/mapfiles/ms/icons/yellow-dot.png';     
     }
     case "ถนน":{
      return 'http://maps.google.com/mapfiles/ms/icons/purple-dot.png';
     
     }
     case "ระบายน้ำและปะปา":{
      return 'http://maps.google.com/mapfiles/ms/icons/blue-dot.png';
     
     }
     case "สวน":{
      return 'http://maps.google.com/mapfiles/ms/icons/green-dot.png';
     
     }
     case "อื่นๆ":{
      return 'http://maps.google.com/mapfiles/ms/icons/pink-dot.png';
     }
     
    }   
  
  }
  Showtype(){ 
    this.loadtype.open();
  }
  
 
}



