import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { EditshowrePage } from '../editshowre/editshowre';
import { HomeshowPage } from '../homeshow/homeshow';


@Component({ template: `
<ion-list>
  <button ion-item  *ngIf="login.user!=null" (click)="Edit()">แก้ไขข้อมูล</button>
</ion-list>
`
})
export class PopovereditPage {
  public login = {user:sessionStorage.getItem("email")};
  constructor(public navCtrl: NavController, public navParams: NavParams) {

    
  }

  Edit()  {
  
    this.navCtrl.push(EditshowrePage);
  
   
  }
 
    
    

  
}



