import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { FirebaseListObservable, AngularFireDatabase } from 'angularfire2/database-deprecated';

import { person } from '../person/person';
import "rxjs/add/operator/map";
import { Observable } from '../../../node_modules/rxjs/Observable';
import { Savestatus } from '../../model/savestatus';
import { reported } from '../reported/reported';
import { StatusedPage } from '../statused/statused';



@Component({
  selector: 'page-editsta',
  templateUrl: 'editsta.html',
})
export class EditstaPage {
  persons : FirebaseListObservable<any>;
 


  constructor(public navCtrl: NavController, 
              public navParams: NavParams,
              public db:AngularFireDatabase) {
    this.persons  = db.list('/report')

              }

              editStatus(id,email,topics,comment,category,MyData,status,image){
                this.navCtrl.push(StatusedPage,{
                  key       : id,
                  email     : email,
                  MyData    : MyData,
                  category  : category,
                  topics    : topics,
                  comment   : comment,
                  status    : status,
                  image     : image
                  
                  
                      });

              }
  


  


}



