import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { AngularFireAuth } from 'angularfire2/auth';
import { ReportPage } from '../report/report';
import { App } from 'ionic-angular/components/app/app';
import { RegisterPage } from '../register/register';
import { HomeshowPage} from '../homeshow/homeshow';
import { LoadingController } from 'ionic-angular';
import { ShowlistPage } from '../showlist/showlist';

@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {

  email: string = '';
  password: string = '';

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public app: App,
    public fire: AngularFireAuth,
    public loadingCtrl: LoadingController)
    {
  }

  myLogIn() {

    this.fire.auth.signInWithEmailAndPassword(this.email, this.password).then(user => {
      console.log(user);
      
      console.log(this.email + "  " + this.password)
      // this.navCtrl.push(ReportPage)
      
      sessionStorage.setItem("email",user.email);
      this.loadingCtrl.create({
        content: "รอสักครู่ ...",
        duration: 10000
      });
      this.navCtrl.setRoot(ShowlistPage);    
      const root = this.app.getRootNav();
            root.popToRoot();
    }).catch(function (error) {
      console.log(error);
      this.navCtrl.push(LoginPage);{
        alert('กรุณากรอกใหม่อีกครั้ง');
      }
      
    });

    console.log("email:" + this.email + "password:" + this.password)
  }

  openSignup() {
    this.navCtrl.push(RegisterPage);
  }

}



