import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { HomePage } from '../home/home';
import { LoginPage } from '../login/login';
import { FirebaseListObservable, AngularFireDatabase } from 'angularfire2/database-deprecated';
import { AngularFireAuth } from 'angularfire2/auth';
import { Person } from '../../model/person';
import { person } from '../person/person';
import { TostServiceProvider } from '../../providers/tost-service/tost-service';
import { PersonProvider } from '../../providers/person/person';

@Component({
  selector: 'page-about',
  templateUrl: 'about.html'
})
export class AboutPage {
    
    persons : Person[];
    person  : Person = { username: "", name: "", lname: ""};

constructor(public fire:AngularFireAuth,public navCtrl: NavController,
    public db: AngularFireDatabase,
    public Tost: TostServiceProvider,
    private personProvider: PersonProvider) {

        fire.auth.onAuthStateChanged(function(user){
if(!user){
    navCtrl.setRoot(LoginPage)
}
        });
   
}

createPerson(username,name,lname){
    this.person = {
        username    : username,
        name        : name,
        lname       : lname
        
    };
    try {
      
        this.personProvider.pushObject(this.person).then(res=>{
          this.Tost.showToastWithCloseButton("บันทึกข้อมูลสำเร็จ");
          this.navCtrl.push(HomePage);
        });
  
      } catch (error) {
        this.Tost.showToastWithCloseButton(error);
      }
  

}

}



