import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { ReportPage} from '../report/report';
import { HomeshowPage } from '../homeshow/homeshow';
import { EditshowrePage } from '../editshowre/editshowre';
import { ShowreportlistPage }from '../showreportlist/showreportlist';
import { StatusPage} from '../status/status';
import { TypeePage} from '../typee/typee';
import { ShowrePage} from '../showre/showre';
import { EditstaPage } from '../editsta/editsta';
@Component({
  selector: 'page-showlist',
  templateUrl: 'showlist.html',
})
export class ShowlistPage {

  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }
  reportnow(){
    this.navCtrl.push(ReportPage);
  }
  problem(){
    this.navCtrl.push(HomeshowPage);
  }
  problemList(){
    this.navCtrl.push(ShowreportlistPage);
  }
  edit(){
    this.navCtrl.push(EditshowrePage);
  }
  status(){
    this.navCtrl.push(StatusPage);
  }
  type(){
    this.navCtrl.push(TypeePage);
  }
  statusType(){
    this.navCtrl.push(ShowrePage);
  }
  editstatus(){
    this.navCtrl.push(EditstaPage);
  }

}



