import { Component, ViewChild } from '@angular/core';
import { /*IonicPage,*/ NavController, Select } from 'ionic-angular';
import { AngularFireAuth } from 'angularfire2/auth';
//import { EditshowrePage } from '../editshowre/editshowre';
import { person } from '../person/person';
import "rxjs/add/operator/map";
import { Observable } from '../../../node_modules/rxjs/Observable';
//import { ShowstatusPage } from '../showstatus/showstatus';
import { FirebaseListObservable, AngularFireDatabase } from 'angularfire2/database-deprecated';
//import { Savestatus } from '../../model/savestatus';
import { LoadingController } from 'ionic-angular';
import { TostServiceProvider } from '../../providers/tost-service/tost-service';
import { ReportProvider } from '../../providers/report/report';
import { EditstaPage } from '../editsta/editsta';
import { PopoverController } from 'ionic-angular';
import { PopoverPage } from '../popover/popover';
import { PopoverTypePage } from '../popover-type/popover-type';

/*import { FixunPage } from '../fixun/fixun';
import { FixingPage } from '../fixing/fixing';
import { FixedPage } from '../fixed/fixed';*/ 
 

@Component({
  selector: 'page-showre',
  templateUrl: 'showre.html',
})
export class ShowrePage {

  status: string;
  category: string;
 
  people : FirebaseListObservable<any>;
  peoples: Array<{category: string, comment:string, lat: number, lng: number, email : string,
  status: string, topics: string, email_unfix : string, myDate: string , image? : string}>;
  
  @ViewChild('typeReport') typeReport: Select;
  constructor(public fire:AngularFireAuth,
              public navCtrl: NavController,
              public db:AngularFireDatabase,
              public loadingCtrl: LoadingController,
              public Tost: TostServiceProvider,
              /*private reportProvider: ReportProvider,*/
              public popoverCtrl:PopoverController) {

              this.category = 'ขยะ'; // กำหนดค่าเริ่มต้น
              this.status = 'ยังไม่ได้แก้ไข'; // กำหนดค่าเริ่มต้น
              this.people = db.list('/report');
              this.loadReport();
             
  }
  public findAllPersons(): Observable<person[]> {
    return this.db.list('report')
    .map(person.fromJsonList);
  
  } 

deletePerson(id) {
  this.people.remove(id);
}

editStatus(id,category,topics,comment,MyData,status,email,image) {

  this.navCtrl.push(EditstaPage,{
  key : id,
  email : email,
  category : category,
  topics : topics,
  comment : comment,
  MyData : MyData,
  status : status,
  image : image
      });
}
  typeTrigger(){
    this.typeReport.open(); // เปิด select ของประเภทที่ซ่อน
  }

  setStatus(status){
    this.status = status; // เก็บสถานะของรายงานที่ดึงข้อมูลมา
    this.loadReport(); // เรียกใช้ฟังค์ชั่นดึกรายงานมาใหม่
  }

  loadReport(){
    if (typeof this.peoples !== 'undefined'){
      if (this.peoples.length > 0){ // length เป็น properties ที่มีเฉพาะในอาร์เรย์ เพราะงั้นต้องตรวจก่อนว่าได้ประกาศเป็นอาร์เรย์แล้ว
        this.peoples.length = 0; // ลบของเก่าออก
      }
    }

    // ดึงข้อมูลจาก firebase ตามสถานะที่เลือก
    this.db.list('/report', {query: {
      orderByChild: 'status',
      equalTo: this.status
    }}).subscribe(reports => {
      reports.forEach(report => { // for อย่างย่อ ข้อสำคัญคือ อาร์เรย์ต้องเป็นฟอร์แมตแบบ [0,1,2,...] ที่สามารถวนได้
        if (report.category === this.category){ // เพราะ firebase สร้างเงื่อนไขใน query ได้แค่หนึ่งฟิลด์ เพราะงั้นต้องกรองประเภทเองตรงนี้
          // ตรวจว่าตัวแปร peoples เป็นอาร์เรย์ที่ถูกประกาศอย่างถูกต้องที่ใช้ฟังค์ชั่น push ได้ ถ้าไม่ก็จะประกาศก่อน
          if (typeof this.peoples === 'undefined'){
            this.peoples = [{
              category: report.category,
              comment: report.comment,
              lat: report.lat,
              lng: report.lng,
              status: report.status,
              topics: report.topics,
              email_unfix : report.email_unfix,
              email : report.email,
              image:report.image,
              myDate: (report.MyData===''? '':report.MyData)
            }];
          }else{
            this.peoples.push({
              category: report.category,
              comment: report.comment,
              lat: report.lat,
              lng: report.lng,
              status: report.status,
              topics: report.topics,
              email_unfix : report.email_unfix,
              email : report.email,
              image:report.image,
              myDate: (report.MyData===''? '':report.MyData)
            });
          }
        }
      });
    });
  }
}



