import { Component } from '@angular/core';
import { IonicPage, NavController } from 'ionic-angular';
import { AngularFireAuth } from 'angularfire2/auth';
import { EditshowrePage } from '../editshowre/editshowre';
import { ReportProvider } from '../../providers/report/report';
import { Report } from '../../model/report';
import { FirebaseListObservable, AngularFireDatabase } from 'angularfire2/database-deprecated';



@Component({
  selector: 'page-showmaps',
  templateUrl: 'showmaps.html',
})
export class ShowmapsPage {


  persons: FirebaseListObservable<any>;

  reports:Report[];

  constructor(
    public fire: AngularFireAuth, 
    public navCtrl: NavController, 
    public db: AngularFireDatabase,
    private reportProvider:ReportProvider
  ){
    this.persons = db.list('/report');
    this.reportProvider.getList().subscribe(list=>{
      console.log("list",list);      
      this.reports = list;
      console.log("this.reports",this.reports);
    });
  }

  deletePerson(id) {
    this.persons.remove(id);
  }


  editPerson(id, opts, addH, addCom, myDate, lat, lng) {
    // console.log(  id, name,  lname, dept);
    this.navCtrl.push(EditshowrePage, {
      key: id,
      opts: opts,
      addH: addH,
      addCom: addCom,
      myDate: myDate,
      lat: lat,
      lng: lng

    });

  }




}



