export class person {
    constructor(
      public $Key: String,
      public email: String,
      public id: String,
      public category: String,
      public topics: String,
      public comment: String, 
      public MyDate: String,
      public status : String,
      public lat: String,
      public lng: String) {
  }
  
  static fromJsonList(array):person[]{
     return array.map(person.fromJson);
  }


  
  static fromJson({$key,id,category,topics,comment,MyDate,lat,lng,status,email}):person {
    return new person(
          $key,
          id,
          email,
          category,
          topics,
          comment,
          MyDate,
          lat,
          lng,
          status
          );
   }
  }


