
export class user {
    constructor(
      public $Key       : String,
      public id         : String,
      public username   : String,
      public name       : String,
      public lname      : String,) {
  }
  
  static fromJsonListUser(array):user[]{
     return array.map(user.fromJson);
  }
  static fromJson({$key,id,username,name,lname}):user {
    return new user(
   $key,
            id,
            username,
            name,
            lname
          );
   }
  }


